import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

// 按需导入Vant组件
import 'vant/lib/index.css';
import './ventui/index'

//  定义全局过滤器
Vue.filter('priceFormat', function (price) {
  return '￥ ' + Number(price).toFixed(2) + ' 元'
});
Vue.config.productionTip = false;
new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
