<template>
  <transition name="myfade">
    <div id="home">
      <!--  搜索区 -->
      <van-search placeholder="请输入搜索关键词" input-align="left"></van-search>
      <!-- 轮播图 -->
      <van-swipe class="home-swipe" :autoplay="3000" indicator-color="#8b0000">
        <van-swipe-item class="home-swipe-item" v-for="(item,index) in banner" :key="index">
          <img class="home-swipe-img" v-lazy="item.image_url" :alt="item.name" />
        </van-swipe-item>
      </van-swipe>

      <!-- 频道列表 -->
      <van-grid class="home-grid-channel" :column-num="5">
        <van-grid-item
          v-for="item in channel"
          :key="item.id"
          :icon="item.icon_url"
          :text="item.name"
          class="van-grid-item"
          @click="handleChannelItemClick(item.url)"
        />
      </van-grid>

      <!-- 品牌制造商供应 -->
      <brand-list :brandList="brandList" />

      <!--新品首发 -->
      <news-list :newGoodsList="newGoodsList" />

      <!-- 人气推荐 -->
      <hot-goods-list :hotGoodsList="hotGoodsList" />

      <!-- 专题精选 -->
      <topic-list :topicList="topicList" />

      <!--  商品列表 -->
      <div class="categoryList">
        <category-list
          v-for="item in categoryList"
          :key="item.id"
          :goodsList="item.goodsList"
          :title="item.name"
        />
      </div>
      <bottom-tip />
    </div>
  </transition>
</template>

<script>
// @ is an alias to /src
import {getHomeData} from "@/network/apis"
import BrandList from "./childComps/BrandList"
import NewsList from "./childComps/NewsList"
import HotGoodsList from "./childComps/HotGoodsList"
import TopicList from "./childComps/TopicList"
import CategoryList from "./childComps/CategoryList"
import BottomTip from '@/components/content/BottomTip'
export default {
  name: "Home",
  data(){
    return {
      // 轮播图
      banner: [],
      // 频道
      channel: [],
      // 品牌推荐
      brandList:[],
      // 新品推荐
      newGoodsList:[],
      // 人气推荐
      hotGoodsList:[],
      // 专题精选
      topicList:[],
      // 商品分类
      categoryList:[],
    }
  },
  components:{
    BrandList,NewsList,HotGoodsList,TopicList,CategoryList,BottomTip
  },
  methods:{
    getHomeData(){
      getHomeData().then(res=>{
        console.log('home==>',res);
        if(res.errno ===0){
          let {banner, channel, brandList,newGoodsList,hotGoodsList,topicList,categoryList} = res.data;
          this.banner = banner;
          this.channel =channel;
          this.brandList =brandList;
          this.newGoodsList =newGoodsList;
          this.hotGoodsList =hotGoodsList;
          this.topicList =topicList;
          this.categoryList =categoryList;
        }
        
      });
    },
    // 事件处理
    // url ==> /pages/category/category?id=1005000
    handleChannelItemClick(url){
      // console.log('ChannelItemClick,item.url==>',url);
      this.$router.push(`/channel/${url.split('=').pop()}`);
    }
  },
  created(){
   this.getHomeData();
  }
};
</script>
<style scoped lang="less">
#home {
  padding-bottom: 50px;

  .home-swipe {
    height: 2rem;
    .home-swipe-img {
      width: 100%;
      height: 2rem;
    }
  }
}

.van-grid-item__text {
  font-size: 0.12rem;
}
/* 首页内容的淡出 */
.myfade-leave {
  opacity: 1;
}
.myfade-leave-active {
  transition: opacity 0.5s linear;
}
.myfade-leave-to {
  opacity: 0;
}
</style>
