import request from "./request"

// 请求主页数据
export function getHomeData() {
  return request({
    url: '/'
  });
}

// 请求主页数据
export function getTopicList(params) {
  return request({
    url: '/topic/list',
    params
  });
}

// 请求分类数据
export function getCategoryList() {
  return request({
    url: '/catalog/index',
  })
}

// 请求当前的分类数据
export function getCategoryItem(params) {
  return request({
    url: '/catalog/current',
    params
  })
}

// 请求当前channel数据
// ?id=1005002
export function getChannelById(params) {
  return request({
    url: '/goods/category',
    params
  })
}

// 请求当前channel下的商品数据
// ?categoryId=1005000&page=1&size=1000
export function getChannelGoodsById(params) {
  return request({
    url: '/goods/list',
    params
  })
}